import { mapMutations } from 'vuex';
export default {
	data() {
		return {
			isWXAppInstalled: false
		};
	},
	onLoad() {
		let WXApi = plus.ios.import('WXApi');
		this.isWXAppInstalled = WXApi.isWXAppInstalled();
	},
	methods: {
		...mapMutations(['setLogin']),
		onSubmit(e) {
			let that=this;
			console.log('提交数据：', e.detail.value);
			this.$Fetch({
				url: 'login/doit',
				data: e.detail.value,
				debug: true,
				success: res => {
					uni.setStorage({
						key: 'TOKEN',
						data: res,
						success: function() {
							console.log(res);
							that.setLogin();
							uni.showToast({
								title: '登录成功',
								icon: 'success'
							});
							setTimeout(() => {
								uni.switchTab({
									url: '/pages/index/index'
								});
							}, 1500);
						}
					});
				}
			});
		},
		wxLogin() {
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log(loginRes.authResult);
				}
			});
		}
	}
};
